.slidebar{
  width: 80px;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background-color:#4eb1f4;
  color:#FFF;
  z-index:100;
  
}
  .slidebar .profile{
      margin-top:20px;
  }
  .slidebar .profile .name{
          overflow: hidden;
          margin: 2px 6px;
      }
  .slidebar .menus{
      margin-top:10px;

  }
  .slidebar .menus .app-item{
          margin-top:10px;
      }
  .slidebar .iconfont{
      font-size:28px;
  }
  .slidebar .app-item{
      text-align: center;
  }
  .slidebar .footer{
      position: absolute;
      bottom: 10px;
      width: 100%;
  }



