/* .bubblePanel {
  margin-left: 320px;
} */
.ctn-bubblepanel{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 321px;
  right: 0;
}
.ctn-bubblepanel .title{
      height:60px;
      border-bottom:1px solid #eee;
      line-height: 60px;
      padding: 0 10px;
  }
.ctn-bubblepanel .ctn-msglist {
      position: absolute;
      top: 61px;
      bottom: 180px;
      width: 100%;
      overflow: auto;
  }
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item {
              overflow:auto;
          }
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item .message-item-outer{
                  display: -webkit-box;
                  display: -ms-flexbox;
                  display: flex;
                  padding: 10px;
                  -webkit-box-sizing: border-box;
                          box-sizing: border-box;
              }
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item.me .message-item-outer{
                      float:right;
}
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item.me .avator-outer {
                      margin-left: 15px;
                      margin-right:0;
}
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item.me .name{
                      text-align: right;
}
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item.me .message-text{
                      background: #4eb1f5;
}
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item.you .message-text{
                      background-color: #e4e4e4;
                      color: #333;
}
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item .avator-outer {
                  margin-right: 15px;
}
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item .message-item-inner .name{
                      font-size:13px;
                  }
.ctn-bubblepanel .ctn-msglist .ctn-msglist-inner .message-item .message-item-inner .message-text {
                      padding: 5px;
                      margin-top: 5px;
                      border-radius: 3px;
                      color: #FFF;
                  }
.ctn-bubblepanel .ctn-msg-sender {
      position: absolute;
      bottom: 0;
      height: 180px;
      border-top: 1px solid #eee;
  }
.ctn-bubblepanel .ctn-msg-sender textarea {
          width: 320px;
          height: 170px;
          border: none;
          resize: none;
          outline:none;
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
          padding:10px;

      }
.ctn-bubblepanel .ctn-msg-sender .button{
          position: absolute;
          right: 10px;
          bottom: 0;
          width: 80px;
      }


